body {
  margin: 0;
  padding: 0;
  font-family: 'Kreon';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:#F5F7FA;
  color: black;
}


img{
  max-width: 100%;
}

a{
  color:black;
  text-decoration: none;
}

#root{
  display: grid;
  grid-template-columns: 100px 90%;
  grid-template-areas: "nav main";
  width: 100vw;
  grid-gap: 1rem;
}

nav{
  grid-area: nav;
  margin-left: 5px;
 
}

main{
  grid-area: main;
}

