.wrapper{
    display: grid;
    grid-template-columns: 30% 30%;
    grid-column-gap:170px;
    grid-row-gap: 3rem;
    justify-content: center;
    margin-top: 180px;
}
p{
    margin:0;
    padding: 0;
}

.left{
    display: flex;
    justify-self: end;
    align-self: end;
}

.right{
    display: flex;
    justify-self: start;
    align-self: end;
}

.tank a:hover {
   opacity: 0.6;
   transition:all 1s ease 0s;
}

@media only screen and (max-width: 600px) {
    .wrapper{
        grid-template-columns: 40%;
    }
    
    }


